<template>
  <div class="repeating-timer">
    <div class="clock-container">
      <clock :initialClockTime="intervals[currentIntervalIndex].intervalTime" v-on:clock-alarm="onClockEnd();" />
    </div>
    <div class="interval-list">
      <div class="interval" :class="{'active': isActiveInterval(index)}" v-for="(item, index) in intervals" :key="item.label">
        <!-- find the active interval with currentIntervalIndex -->
        {{ item.index }}
        <div class="interval-label">{{ item.label }}</div>
        <div class="interval-time">{{ readableTime(item.intervalTime) }}</div>
      </div>
      <div class="spacer"></div>
      <div class="repeat-option">
        <input id="repeat-option" type="checkbox" :checked="repeatForever" />
        <label for="repeat-option">Repeat Forever</label>
      </div>
    </div>
  </div>
</template>

<script>
import Clock from "@/components/Clock";

export default {
  name: 'RepeatingTimer',
  props: {
    repeatForever: {
      default: true,
      type: Boolean
    },
    repeatCount: Number,
  },
  data() {
    return {
      intervals: [
        {
          label: 'Nerd Time',
          intervalTime: 3600,
          alarmSound: '../assets/sounds/alerts/david-goggins-1-lobsterdinner.mp3',
        },
        {
          label: 'Exercise Window',
          intervalTime: 600,
        },
      ],
      currentIntervalIndex: 0,
    }
  },
  methods: {
    onClockEnd() {
      //send clock alert and start the next clock interval if it exists
      const currentInterval = this.intervals[this.currentIntervalIndex];
      if(currentInterval){
        //var audio = new Audio(require(currentInterval.alarmSound));
        var audio = new Audio(require('../assets/sounds/alerts/david-goggins-1-lobsterdinner.mp3'));
        audio.play();
        console.log('lets go');
      }
      if(this.currentIntervalIndex < (this.intervals.length - 1)){
        this.currentIntervalIndex += 1;
      } else if (this.repeatForever) {
        this.currentIntervalIndex = 0;
      }
    },
    readableTime(time){
      //convert seconds into readable time
      const minutes = Math.floor(time / 60);
      let seconds = time % 60;
      if (seconds < 10) seconds = `0${seconds}`;

      return `${minutes}:${seconds}`;
    },
    isActiveInterval(intervalIndex){
      if(intervalIndex == this.currentIntervalIndex){
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
  },
  components: {Clock},
}
</script>

<style lang="scss" scoped>
  .repeating-timer {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    .clock-container {
      padding: 5%;
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .interval-list {
      display: flex;
      align-items: center;
      flex-direction: column;
      background-color: $background-foreground-color;
      height: 100vh;

      $interval-spacing: 25px;
      .interval {
        padding: $interval-spacing 20px;
        width: 250px;
        font-size: 1em;
        display: flex;
        color: $text-supplemental-color;
        border-bottom: 1px solid $border-dark;
        background-image: linear-gradient(rgba(black, 0), rgba(black, 0.02));
        &.active {
          //background-image: linear-gradient(rgba(red, 0.2), rgba(blue, 0.2));
          font-weight: bold;
          color: $text-highlight-color;
        }
        .interval-label {
          flex-grow: 1;
        }
        .interval-time {
          margin-left: 15px;
          text-align: right;
        }
      }
      .spacer {
        flex-grow: 1;
      }
      .repeat-option {
        color: $text-supplemental-color;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 20px;
        background-color: rgba($background-dark-color, 0.2);
        border-top: 1px solid $border-dark;
        * {
          opacity: 0.6;
        }
        label {
          user-select: none;
        }
      }
    }
  }
</style>
